import React, { Component } from 'react';
import Link from 'gatsby-link'
import './header.css'

class Header extends Component {
  constructor(props) {
    super(props);

    this.handleToggle = this.handleToggle.bind(this);
  }

  handleToggle() {
    this.props.onToggle();
  }

  render() {    
    return (
      <header className="header">
        <nav>
          <Link
            to="/"
            className="brandName"
          >
            ThapGan's Blog
          </Link>
          <div style={{
            float: 'right',
            height: '50px',
            display: 'inline-block',
            lineHeight: '50px'
          }}>
            <i className="navbarToogle fas fa-bars" onClick={(e)=>{this.handleToggle(e)}}/>
            <ul className="navbarRight">
              {
                this.props.categories.map(category => {
                  return <li key={category.name}><Link to={`/category/${category.name}`} activeClassName="menu-active">{category.name}</Link></li>
                })          
              }
            </ul>
          </div>
        </nav>
      </header>
    )
  }
}
export default Header



