const config = require("./site-config.json");

module.exports = {
  pathPrefix: config.pathPrefix === "/" ? "" : config.pathPrefix,
  siteMetadata: {
    title: config.title,
    siteUrl: config.siteUrl + config.pathPrefix,
    description: config.description,
    feed_url: config.siteUrl + config.pathPrefix + '/rss.xml',
    author: config.author,
    copyright: config.copyright
  },
  plugins: [
    'gatsby-plugin-react-helmet',
    'gatsby-plugin-sharp',
    {
      resolve: 'gatsby-plugin-mailchimp',
      options: {
        endpoint: config.mailchimp, // see instructions section below
      },
    },
    {
      resolve: `gatsby-source-filesystem`,
      options: {
        name: `pages`,
        path: `${__dirname}/src/pages/`,
      },
    },
    {//gatsby-source-filesystem
      resolve: 'gatsby-source-filesystem',
      options: {
        path: `${__dirname}/posts`,
        name: 'markdown-pages',
      },
    },
    {//gatsby-transformer-remark
      resolve: 'gatsby-transformer-remark',
      options: {
        plugins: [
          'gatsby-remark-katex',
          'gatsby-remark-prismjs',
          'gatsby-remark-autolink-headers',
          {
            resolve: 'gatsby-remark-images',
            options: {
              // It's important to specify the maxWidth (in pixels) of
              // the content container as this plugin uses this as the
              // base for generating different widths of each image.
              maxWidth: 590,
            }
          }
        ]
      }
    },
    'gatsby-plugin-sitemap',
    {//gatsby-plugin-feed
      resolve: "gatsby-plugin-feed",
      options: {
        query: `
        {
          site {
            siteMetadata {
              title
              description
              siteUrl
              site_url: siteUrl
              feed_url
              author
              copyright
            }
          }
        }`,
        feeds: [
          {
            serialize(ctx) {
              const rssMetadata = ctx.query.site.siteMetadata;
              return ctx.query.allMarkdownRemark.edges.map(edge => ({
                categories: edge.node.frontmatter.tags,
                date: edge.node.frontmatter.date,
                title: edge.node.frontmatter.title,
                description: edge.node.excerpt,
                author: rssMetadata.author,
                url: rssMetadata.site_url + edge.node.frontmatter.path,
                guid: rssMetadata.site_url + edge.node.frontmatter.path
                // custom_elements: [{ "content:encoded": edge.node.html }]
              }));
            },
            query: `
            {
              allMarkdownRemark(
                limit: 1000,
                sort: { order: DESC, fields: [frontmatter___date] },
              ) {
                edges {
                  node {
                    excerpt
                    timeToRead
                    frontmatter {
                      path
                      title                      
                      date
                      category
                      tags
                    }
                  }
                }
              }
            }
          `,
            output: "/rss.xml"
          }
        ]
      }
    }
  ],
};



