import React from 'react'
import PropTypes from 'prop-types'

import Helmet from 'react-helmet'
import { Link, withPrefix } from 'gatsby-link'

import Header from '../components/Header'
import TagList from '../components/TagList'
import AuthorLinks from "../components/AuthorLinks"
import SubscriptionForm from "../components/SubscriptionForm"

import './index.css'
import './prism-okaidia.css'

class Layout extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      toggle: false
    }

    this.toggleHandler = this.toggleHandler.bind(this);
  }

  toggleHandler() {
    this.setState({ toggle: !this.state.toggle });
  }

  render() {
    const siteConfig = require('../../site-config.json');

    const tags = [...siteConfig.categories, ...tags];
    return (
      <div className="page-content">
        <Helmet>
          <title>{siteConfig.title}</title>
          <meta name="description" content={siteConfig.description} />
          <meta name="keywords" content={siteConfig.keywords} />
          <script src="https://cdnjs.cloudflare.com/ajax/libs/tocbot/4.1.1/tocbot.min.js"></script>
          <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/KaTeX/0.9.0-beta/katex.min.css" integrity="sha384-L/SNYu0HM7XECWBeshTGLluQO9uVI1tvkCtunuoUbCHHoTH76cDyXty69Bb9I0qZ" crossorigin="anonymous" />
          <link href="https://use.fontawesome.com/releases/v5.0.6/css/all.css" rel="stylesheet" />
        </Helmet>
        <Header categories={siteConfig.categories} onToggle={this.toggleHandler} />
        <div className={`left-menu ${this.state.toggle ? '' : 'hidden'}`}>
          <img src={withPrefix('/avatar.jpg')} alt="avatar" />
          <h5><a href={siteConfig.authorLinks.home? siteConfig.authorLinks.home : siteConfig.siteUrl + siteConfig.pathPrefix}>ThapGan's Blog</a></h5>
          <AuthorLinks authorLinks={siteConfig.authorLinks}/>
          <TagList tags={siteConfig.categories} prefix="category" clickHandler={this.toggleHandler} key="categories"/>
          <TagList tags={siteConfig.tags} prefix="tag" clickHandler={this.toggleHandler} key="tags"/>
          <SubscriptionForm/>
        </div>
        {this.props.children()}        
      </div>
    )
  }
}

export default Layout;



