import React from "react"
import Link, { withPrefix } from 'gatsby-link'

import "./authorLinks.css"

export default function AuthorLinks(props) {
    let { authorLinks } = props;

    return <ul className="social-list">
        {authorLinks.facebook &&
            <li>
                <a target="_blank" href={authorLinks.facebook}>
                    <span className="fa-stack fa-lg">
                        <i className="fa fa-circle fa-stack-2x" />
                        <i className="fab fa-facebook-f fa-stack-1x fa-inverse" />
                    </span>
                </a>
            </li>
        }

        {authorLinks.youtube &&
            <li>
                <a target="_blank" href={authorLinks.youtube}>
                    <span className="fa-stack fa-lg">
                        <i className="fa fa-circle fa-stack-2x" />
                        <i className="fab fa-youtube fa-stack-1x fa-inverse" />
                    </span>
                </a>
            </li>
        }

        {authorLinks.twitter &&
            <li>
                <a target="_blank" href={authorLinks.twitter}>
                    <span className="fa-stack fa-lg">
                        <i className="fa fa-circle fa-stack-2x" />
                        <i className="fab fa-twitter fa-stack-1x fa-inverse" />
                    </span>
                </a>
            </li>
        }

        {authorLinks.github &&
            <li>
                <a target="_blank" href={authorLinks.github}>
                    <span className="fa-stack fa-lg">
                        <i className="fa fa-circle fa-stack-2x" />
                        <i className="fab fa-github fa-stack-1x fa-inverse" />
                    </span>
                </a>
            </li>
        }
        <li>
            <a target="_blank" href={withPrefix("/rss.xml")}>
                <span className="fa-stack fa-lg">
                    <i className="fa fa-circle fa-stack-2x" />
                    <i className="fas fa-rss fa-stack-1x fa-inverse" />
                </span>
            </a>
        </li>
        <li>
            <Link to={withPrefix("/search")}>
                <span className="fa-stack fa-lg">
                    <i className="fa fa-circle fa-stack-2x" />
                    <i className="fas fa-search fa-stack-1x fa-inverse" />
                </span>
            </Link>
        </li>
    </ul>
}



