import React from "react";
import Link, { withPrefix } from "gatsby-link";
import "./tag-list.css"

export default function TagList({ tags = [], prefix = '', clickHandler }) {
  return (
    <div className="tagList">
      {
        tags.map(tag => {
          return <Link 
                    to={`/${prefix}/${tag.name}`} 
                    key={tag.name} 
                    title={tag.name} 
                    rel={tag.rel}
                    onClick={clickHandler}
                  >{tag.name}</Link>
        })
      }
    </div>
  );
}



